<!--  -->
<template>
		<div class="background">
		<div class="addbox"  @click="goSelectAddress()">
			<div class="bigBox">
				<div class="addFont">
					<div>
						<p class="nameFont">{{addressInfo.postName}}</p>
						<p class="nameFont">{{addressInfo.postPhone}}</p>
					</div>
					<p class="add">{{addressInfo.postAddress}}</p>
				</div>
			</div>
			<div class="buttonImg"><img src="../assets/images/icon/rightGray.png"></div>
		</div>
		<div class="comBox" v-for="(ite,key,index) in listDetailInfo" :key='key'>
			<img :src="item.coverResource.cover || ImgNull" class="comImg">
			<div class="specName">
				<p class="comName">{{item.goodsName}}</p>
				<p class="spec">{{item.sizeName}}</p>
				<div>
					<p class="money">{{item.price}}</p>
					<div class="number">x{{item.number}}</div>
				</div>
			</div>
		</div>
		<div class="payBox">
			<div class="payFont"></div>
		</div>
		<div class="payName">
			<div class="payWeChat" v-for="item in dataPay" :key="item.id" @click="selectPay(item.id)">
				<div class="imgWeChat">
					<img :src="item.img" />
					<p>{{item.payFont}}</p>
				</div>
				<div class="selectImg">
					<img v-if="!item.isshow" src="../assets/images/icon/sele.png" />
					<img v-if="item.isshow" src="../assets/images/icon/select.png" />
				</div>
			</div>
		</div>
		<div class="integralBox">
			<div class="integralFont">
				<p class="uses"></p>
				<p class="numberInt">{{scoreBalInfo}}0000{{maxScoreInfo}}</p>
			</div>
			<img :src="shutOpent" @click="shutClick" />
		</div>
		<div class="bottomBox">
			<div class="bottomMoney">{{cashBalInfo}}</div>
			<div class="bottomPay"  @click="submitMoney"></div>
		</div>
	</div>
</template>

<script>
import { Toast } from 'we-vue';
import * as api from '@/assets/js/api';
import * as session from '@/assets/js/session';
export default {
	name: 'placeOrder',
	data() {
		return {
			cashBalInfo: '', //
			scoreBalInfo: '', //
			maxScoreInfo: '', //
			orderInfo: '', //
			addressInfo: '', //
			listDetailInfo: '', //
			shutOpent: require('../assets/images/icon/shut.png'),
			shutOpentId: 1, //1=2=
			dataPayId: 1, //1=2=
			dataPay: [
				{
					id: 1,
					img: require('../assets/images/icon/wechat.png'),
					payFont: '',
					isshow: true
				},
				{
					id: 2,
					img: require('../assets/images/icon/wallet.png'),
					payFont: '',
					isshow: false
				}
			],
			ImgNull: this.$store.state.ImgNull
		};
	},
	created: function() {
		this.$store.commit('showBottomNav', {
			isShow: false
		});
		this.orderId = session.Lstorage.getItem('orderId');
		// 
		api.getConfirmOrder({
			data: {
				openid: this.globalData.openid,
				orderid: this.orderId
			}
		}).then(res => {
			if (res.data.flag) {
				console.log('', res.data);
				this.cashBalInfo = res.data.cashBal; //
				this.scoreBalInfo = res.data.scoreBal; //
				this.maxScoreInfo = res.data.maxScore; //
				this.orderInfo = res.data.order; //
				this.addressInfo = res.data.address; //
				this.listDetailInfo = res.data.listDetail; //
			} else {
				Toast.text({
					duration: 1000,
					message: res.data.msg
				});
			}
		});
	},
	methods: {
		submitMoney: function() {
			//   
			console.log(this.dataPayId, '1=2=');
			console.log(this.shutOpentId, '1=');
		},

		shutClick: function() {
			if (this.shutOpent == require('../assets/images/icon/shut.png')) {
				this.shutOpent = require('../assets/images/icon/open.png');
				this.shutOpentId = 2;
			} else {
				this.shutOpent = require('../assets/images/icon/shut.png');
				this.shutOpentId = 1;
			}
		},
		selectPay: function(id) {
			this.dataPayId = id;
			for (var i = 0; i < this.dataPay.length; i++) {
				if (id == this.dataPay[i].id) {
					this.dataPay[i].isshow = true;
				} else {
					this.dataPay[i].isshow = false;
				}
			}
		},
		goSelectAddress: function() {
			// 
			this.$router.push({
				name: 'selectAddress'
			});
		}
	}
};
</script>

<style scoped="scoped" lang="scss">
@import '@/assets/css/placeOrder.scss';
</style>
